var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{attrs:{"id":"app"}},[_c('StaticHeader'),_c('spacer'),_c('h2',{staticClass:"text-center"},[_vm._v("Automated Instagram Plant Posts")]),_c('spacer'),_c('SectionTitle',{attrs:{"title":"Intro"}}),_c('LeftRight',{attrs:{"img_alt":"alt desc","img":"compscrop.jpg","msg":"<p>A friend of mine showed me this cool post about a plant that automatically posts tweets based on the soil moisture (<a href='http://sarabee.github.io/2020/05/10/plant-tweets-part-2/'>link</a>). I have done some similar work on my own home and thought I could grab this post and take it a step further. In this post I will teach you how to use an arduino and a raspberry pi with a camera to take a picture of the your plant, record the soil moisture, and create an instagram post.</p><p>If you just want the source code, you can scroll all the way to the bottom and find a link to my Github repo.</p>"}}),_c('spacer'),_c('parts-list',{attrs:{"parts":['Arduino Nano (but pretty much any arduino would do) {<a href=\'https://www.sparkfun.com/products/15590\'>Link</a>}',
                 'Raspberry Pi Zero W with camera {<a href=\'https://www.sparkfun.com/products/15470\'>Link</a>}',
                 'Capacitive Soil Sensor {<a href=\'https://www.amazon.com/Gikfun-Capacitive-Corrosion-Resistant-Detection/dp/B07H3P1NRM/ref=sxts_sxwds-bia-wc-nc-drs1_0\'>Link</a>}',
                 'Buncha wires'],"title":"Parts List"}}),_c('spacer'),_c('SectionTitle',{attrs:{"title":"Overall Architecture"}}),_c('b-container',{},[_c('b-img',{attrs:{"src":require('@/assets/Arch.jpg'),"fluid":"","alt":"Architecture diagram"}})],1),_c('b-container',{},[_c('b-row',[_c('b-col',[_c('ol',{staticClass:"text-left"},[_c('li',[_vm._v("The soil sensor detects the moisture on the soil and outputs a voltage")]),_c('li',[_vm._v("The voltage is read by the arduino board via an analog read pin")]),_c('li',[_vm._v("The Arduino uses the voltage and determines if the soil is dry or wet")]),_c('li',[_vm._v("The Arduino then sends the moisture via USB to the Raspberry Pi")]),_c('li',[_vm._v("The Raspberry Pi reads the data from the USB, parses it, takes a picture and creates a package")]),_c('li',[_vm._v("This package is then sent to the internet via wifi")])])])],1)],1),_c('spacer'),_c('spacer'),_c('SectionTitle',{attrs:{"title":"Configuring the Arduino"}}),_c('spacer'),_c('LeftRight',{attrs:{"img":"ArduinoCrop.jpg","img_alt":"Arduino Nano and moisture sensor","msg":"<p>The soil sensor that I bought has three pins, GND, VCC, and Analog Out. Because of its low power requirements, I decided to power it up using the mcu using the 5V and Ground pins. The Analog output pin will give us the voltage based on how humid or dry the soil is, so I connected it to the Arduino's A0 pin.</p>   <p>In order to understand the readings bit better, I decided to translate the voltage to a percentage. The mcu reads a value between 0-1024, so it is not super clear. It is much easier (for a human) to understand '90% humid' than '503 humid'.</p>\n          <p>Finally, we send the readings to the Raspberry Pi via USB using Serial command.</p>\n    \n          <b-row>\n            <b-col cols='3'></b-col>\n            <b-col cols='2' class='text-center' style='background-color:#aba0a0'><p style='font-size:larger'>Gist code <a href='https://gist.github.com/cjc061000/5adf9ae145075afc2818c1db3dc78537'>here</a></p></b-col>\n            <b-col cols='6'></b-col>\n          </b-row>\n\n          <p>Note: These things have to be calibrated in order to work properly, read more about it <a href='https://makersportal.com/blog/2020/5/26/capacitive-soil-moisture-calibration-with-arduino'>here</a>. It can be a dull read, so the tl;dr: measure in water and in air and those will be your max vals. Then look at my code for a working example.</p>"}}),_c('spacer'),_c('SectionTitle',{attrs:{"title":"Configuring the Pi - Serial"}}),_c('spacer'),_c('b-container',{},[_c('b-row',[_c('p',[_vm._v("Configuring the Pi was a bit more challenging. On top of that the Raspberry Pi is the device that is doing all the heavy lifting, taking pictures, and uploading everything to the internet, so I will split this section for each individual task to make it easier to follow.")]),_c('p',[_vm._v("Started by installing the "),_c('a',{attrs:{"href":"https://arduino.github.io/arduino-cli/getting-started/"}},[_vm._v("Arduino CLI")]),_vm._v(" and proceeded to look for my board. I noticed though that I could not find my board on my list of connected devices.")])]),_c('b-row',[_c('CodeSnippet',{attrs:{"code":"$ arduino-cli board list\nPort         Type              Board Name              FQBN                 Core\n/dev/ttyACM1 unknown unknown\n/dev/ttyUSB0 Serial Port (USB) unknown"}})],1),_c('b-row',[_c('p',[_vm._v("Maybe the connection still works just the name is goofed up? I tried Sara's command (tail) to read the serial input, but it was not really working. So instead I tried using cat instead. It worked! Since Linux treats ports as files, I was able to read it it, and I saw the input coming in at regular intervals!")])]),_c('b-row',[_c('CodeSnippet',{attrs:{"code":"$ cat /dev/ttyUSB0\nHumidity: 0%\nHumidity: 0%\n..."}})],1),_c('b-row',[_c('p',[_vm._v("Turns out, the board appearing as \"unknown\" is a known issue and explained better in this "),_c('a',{attrs:{"href":"https://forum.arduino.cc/index.php?topic=676741.0"}},[_vm._v("post")]),_vm._v(". But with the Raspberry connected, now is time to read the data. We use python (pyserial) to read the serial data coming from the arduino. Because I dont know if I have read a complete message (we are reading bytes as they come), I decided that a \"complete\" message must end in a new line character.")])]),_c('b-row',[_c('CodeSnippet',{attrs:{"code":"ser = serial.Serial('/dev/ttyUSB0', 9600, timeout=5)\nsample = ser.read(200) # pull 200 bytes off serial\nsample_list = sample.decode('utf-8').split('\\n')\n...\nreading = reading.strip()\n  reading_kv = reading.split(':')\n  if len(reading_kv) == 2:\n    key = reading_kv[0]\n    val = reading_kv[1]\n    if key == 'Percent':\n      return val\n...\n"}})],1)],1),_c('spacer'),_c('SectionTitle',{attrs:{"title":"Configuring the Pi - Taking a picture"}}),_c('spacer'),_c('b-container',[_c('b-row',[_c('b-col',{attrs:{"cols":"12"}},[_c('p',[_vm._v("The Raspberry Pi Zero has a nifty webcam that I am going to use to take pictures of my plants. So that whenever I make a reading, I can also attach an image to it and post it on Instagram. We start by making sure the camera works well, in order to do that, I followed this guide of "),_c('a',{attrs:{"href":"https://picamera.readthedocs.io/en/release-1.13/quickstart.html"}},[_vm._v("Picamera")]),_vm._v(" to take a test photo using the raspistill command.")])])],1),_c('b-row',[_c('code-snippet',{attrs:{"code":"raspistill -o image.jpg"}})],1),_c('b-row',[_c('p',[_vm._v("At first I had trouble installing raspistill. It seemed that the PATH did not have the correct value for raspistill, so I had to add it manually myself, or call it using the whole path.")]),_c('p',[_vm._v("After a successful test that verifies the cam is working, the next step is to add code to our python script so we can take a picture programmatically. Install the "),_c('a',{attrs:{"href":"https://picamera.readthedocs.io/en/release-1.13/"}},[_vm._v("PiCamera")]),_vm._v(" python library via pip, then you can use it like the snippet below:")])]),_c('b-row',[_c('CodeSnippet',{attrs:{"code":"import PiCamera\ndef TakePicture():\n  camera = PiCamera()\n  camera.start_preview()\n  sleep(4) #camera needs a sec to focus. 2 secs min\n  fileName = \"plantcam.jpg\"\n  camera.capture(fileName)\n  camera.stop_preview()"}})],1)],1),_c('spacer'),_c('SectionTitle',{attrs:{"title":"Configuring the Pi - Upload image to IMGUR"}}),_c('spacer'),_c('b-container',[_c('b-row',[_c('b-col',{attrs:{"cols":"12"}},[_c('p',[_vm._v("As of right now, Instagram does not allow automated tools to create new posts via their API, so I had to find a way to make it easy for myself to create a new post using the information already gathered. I decided then to send myself an MMS using Twilio that will contain all the info necessary to create a post manually.")])])],1),_c('b-row',[_c('b-col',{attrs:{"cols":"12"}},[_c('p',[_vm._v("In order to send an MMS, Twilio requires your picture to be hosted online. We can use IMGUR's API to upload the image. In order to do this you will need a bearer token for authentication. Follow these steps to get one:")]),_c('ol',[_c('li',[_vm._v("Create an account on imgur.com")]),_c('li',[_c('a',{attrs:{"href":"https://api.imgur.com/oauth2/addclient"}},[_vm._v("Register a new application")]),_vm._v(". This will provide you with an application ID (AppID) and an application secret (AppSecret).")]),_c('li',[_vm._v("Using postman, in the Authorization section, select OAUTH2 and fill out the fields as shown in this image (remember to replace {} variables with your appId and AppSecret). Record the bearer token and refresh tokens."),_c('br'),_c('a',{attrs:{"href":"require('@/assets/imgur_postman.jpg')"}},[_c('b-img',{staticStyle:{"max-height":"200px"},attrs:{"thumbnail":"","fluid":"","alt":"Postman reference","src":require('@/assets/imgur_postman.jpg')}}),_c('div',[_vm._v("Click to enlarge")])],1)])])])],1),_c('b-row',[_c('b-col',[_c('p',[_vm._v("You can follow the "),_c('a',{attrs:{"href":"https://apidocs.imgur.com/"}},[_vm._v("official guide here")]),_vm._v(" for more details.")]),_c('p',[_vm._v("With your new tokens you can upload images using the following code, and record the final url:")]),_c('CodeSnippet',{attrs:{"code":"multipart_form_data = {\n    'image': (pictureName, open(_picturePath+pictureName, 'rb')),\n    'type': (None, 'file'),\n    'name': (None, pictureName),\n    'title': (None, 'Automatic picture upload')\n}\nheaders = {'Authorization': _imgurBearerToken}\n\nresponse = requests.post(_imgurAPIUploadUrl, files=multipart_form_data,  headers=headers)\nresponseObj = json.loads(response.content)\nimgurLink = responseObj['data']['link']"}})],1)],1)],1),_c('spacer'),_c('SectionTitle',{attrs:{"title":"Configuring the Pi - Sending MMS with Twilio"}}),_c('spacer'),_c('b-container',[_c('b-row',[_c('b-col',[_c('p',[_vm._v("Next step is to send the actual MMS. I chose Twilio because it is SO cheap ($0.02 per message) and fairly straightforward to send a message. The first step is to create an account and get your own SID and Secret from their portal. I got it from twilio.com/console. Then you will have to pip install the Twiolio REST library")]),_c('CodeSnippet',{attrs:{"code":"pip install twilio"}}),_c('p',[_vm._v("Then you can use the following code to send your MMS.")]),_c('CodeSnippet',{attrs:{"code":"from twilio.rest import Client\n\n  def SendMMS(self, message, imageLink):\n    account_sid = TWILIO_ACCOUNT_SID\n    auth_token = TWILIO_SECRET\n    apiKeySID = TWILIO_APIKEY\n    client = Client(apiKeySID,auth_token, account_sid)\n\n    message = client.messages \\\n        .create(\n            body = message,\n            from = fromNumber,\n            to = toNumber,\n            media_url=[imageLink],\n        )"}})],1)],1),_c('b-row')],1),_c('spacer'),_c('SectionTitle',{attrs:{"title":"Finished Product + Next Steps"}}),_c('spacer'),_c('b-container',[_c('b-row',[_c('b-col',[_c('RightLeft',{attrs:{"msg":"<p>Putting all these components together, and you have a system now that checks the soil of your plant, takes a picture, and sends you an MMS with all the info you need to create your Instagram post. Too bad Instagram does not have this function, so we just had to improvise, adapt and overcome. I bought a couple of enclosures for the nano and the raspberry pi, and a couple of shrink tubes to protect the device to try to give it a bit more of a consumer product look.</p><p>As for 'next steps', I would like to improve the trigger to take a soil reading. Right now its polling ever X seconds, but a more sophisticated approach would be to either have the Pi send the trigger signal, and wait for a response from the device. Most likely using the USB as a 2 way communication port.</p><p>Hope you liked this project. You can find the complete source code on my <a href='https://github.com/KeenHausOrg/DetectorSendData'>GITHUB Repo</a></p>","img":"finished.jpg","img_alt":"Finished product"}})],1)],1)],1),_c('spacer'),_c('Footer')],1)}
